// Для импорта в диспетчер подключений: @import './src/less/blocks/block.less';


.block {
  height: 80vh;
  display: inline-block;
  width: 100%;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 30px;
  border-bottom: 1px solid @gray-color;
  padding-top: 7%;
  &--how {
    // padding-top: 7%;
    justify-content: flex-end;
  }

  &--result .block__item {
    display: block;
    white-space: nowrap;
    margin-bottom: 10px;
    text-indent: 0;
  }
  &--result .block__text--bold {
    height: 55px;
  }

  &__main-title {
    // margin-top: 7%;
    .text(@fake-white-color, 16.25em);
    font-weight: 700;
    line-height: 175px;
    height: 175px;
    width: 100%;
    text-align: left;

    &:before {
      content: '';
      display: inline-block;
      height: 100%;
      background-image: url('/img/main_bgr.png');
      background-repeat: no-repeat;
      width: 110%;
      position: absolute;
      z-index: -1;
      right: 0;
      top: 0;
      background-size: cover;
    }
  }

  &__btn {
    .btn(240px, @black-color, @gray-color, @blue-color);
    height: 40px;
    line-height: 39px;
    font-size: 1em;
    margin-top: 40px;
    text-transform: uppercase;
    text-align: left;
    padding-left: 20px;
    font-weight: 500;
  }

  &__logo-wrapper {
    height: 65px;
    width: 100%;
    position: relative;
    margin-bottom: 30px;
  }

  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: auto;
  }

  &__title {
    .text(@black-color, 1.6em);
    text-transform: uppercase;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 10%;
  }

  &__list {


  }

  &__text {
    .text(@black-color, 0.8em);
    display: block;

    &--bold {
      font-weight: 600;
      height: 115px;
    }
  }

  &__item {
    text-indent: 15px;
    list-style-type: none;
    .text(@black-color, 0.8em);
    margin-bottom: 35px;
    line-height: 1.5;
  }

  &__col {
    width: 50%;
    padding-left: 13%;
    padding-right: 5%;

    &--how {
      width: 35%;
      padding-left: 5%;
      padding-right: 2%;
    }
  }
}
