// Для импорта в диспетчер подключений: @import './src/less/blocks/tarif.less';


.tarif {
  flex-basis: 25%;

  &__title {
    .text(@black-color, 1.6em);
    text-transform: uppercase;
    font-weight: 700;
    line-height: 1;
    width: 100%;
    text-align: center;
  }
  &__descr-wrapper {
    width: 100%;
    text-align: center;
    padding: 50px 0;

  }
  &__price {
    .text(@black-color, 3.2em);
    text-transform: uppercase;
  }
  &__descr {
    .text(@black-color, 1.2em);
    text-transform: uppercase;
  }
  &__list {
    padding-left: 70px;
  }
  &__item {
    .text(@black-color, 0.8em);
    text-transform: uppercase;
    line-height: 3;
  }





  &__wrapper {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    padding: 0 5%;
    justify-content: space-between;
    width: 100%;
  }
}
